// components
import Avatar from "@ui/Avatar";
import { NavLink } from "react-router-dom";
import CryptoIcon from "@ui/CryptoIcon";
import Fade from "@mui/material/Fade";

// utils

import { addZero, formatNumber } from "@utils/helpers";

export const getPeriod = (period, value, average = false, rate = 1) => {
  const yearValue = value['1day'] * rate;
  const monthValue = value['30day'] * rate;
  const weekValue = value['7day'] * rate;
  const total = value['allTime'];
  

  switch (period.value) {
    default:
    case "all":
      return  total;
    case "year":
      return yearValue;
    case "month":
      return monthValue;
    case "week":
      return weekValue;
  }
};

const CellWrapper = ({ children }) => {
  return (
    <Fade in={true} timeout={1000}>
      {children}
    </Fade>
  );
};

export const CollectionCell = ({ value }) => {
  return (
    <CellWrapper>
      <div className="d-flex align-items-center g-15">
        <Avatar src={value.image} alt={value.image} size="sm" />
        <NavLink to={`/author/${value.id}`}>{value.name}</NavLink>
      </div>
    </CellWrapper>
  );
};

export const CryptoCell = ({ value, type, period }) => {
  const rate = type.value === "eth" ? 1 : 2866.96;

  return (
    <CellWrapper>
      <div className="d-flex align-items-center justify-content-center g-10 text-uppercase">
        {value && (
          <>
            <CryptoIcon crypto={type.value} />
            {formatNumber(getPeriod(period, value, true, rate))}
          </>
        )}
      </div>
    </CellWrapper>
  );
};

export const ImageCell = ({ value }) => {
  return (
    <CellWrapper>
      <div className="d-flex align-items-center justify-content-center g-10 text-uppercase">
      <Avatar src={value.sampleImages[0]} size="xs" />
      <Avatar src={value.sampleImages[1]} size="xs" />
      <Avatar src={value.sampleImages[2]} size="xs" />
      <Avatar src={value.sampleImages[3]} size="xs" />
      
      </div>
    </CellWrapper>
  );
};

export const FloorCell = ({ value, type }) => {
    const rate = type.value === "eth" ? 1 : 2866.96;
  
    return (
      <CellWrapper>
        <div className="d-flex align-items-center justify-content-center g-10 text-uppercase">
          {value && (
            <>
              <CryptoIcon crypto={type.value} />
              { //Check if message failed
        (value.price == null)
          ? 0
          : formatNumber(value.price.amount.decimal)
      }
            </>
          )}
        </div>
      </CellWrapper>
    );
  };

export const PercentCell = ({ value, period }) => {
  const displayValue = value ? getPeriod(period, value, true) : 0;

  return (
    <CellWrapper>
      <span className={displayValue > 0 ? "text-accent" : "text-body"}>
        {value &&
          `${displayValue > 0 ? "+" : ""}${formatNumber(displayValue)}%`}
      </span>
    </CellWrapper>
  );
};

export const TextCell = ({ value }) => {
  return (
    <CellWrapper>
      <span className="text-uppercase">
        {value}
      </span>
    </CellWrapper>
  );
};

